// components/D3ConfusionMatrix.tsx
import React, { useRef, useEffect } from 'react'
import { Chart, registerables } from 'chart.js'
import { MatrixController, MatrixElement } from 'chartjs-chart-matrix'
import 'chartjs-chart-matrix'

// register core chart.js components and matrix controller
Chart.register(...registerables, MatrixController, MatrixElement)

interface D3ConfusionMatrixProps {
  data: number[][]
  labels: string[]
  title: string
  width: number
  height: number
}

const D3ConfusionMatrix: React.FC<D3ConfusionMatrixProps> = ({
  data,
  labels,
  title,
  width,
  height
}) => {
  const canvasRef = useRef<HTMLCanvasElement>(null)

  useEffect(() => {
    if (!canvasRef.current) return
    const ctx = canvasRef.current.getContext('2d')
    if (!ctx) return

    // destroy old instance if exists
    const old: any = (canvasRef.current as any).chartInstance
    if (old) old.destroy()

    // flatten data into {x, y, v} points
    const matrixData = data.flatMap((row, y) =>
      row.map((v, x) => ({ x, y, v }))
    )
    const maxVal = Math.max(...matrixData.map(d => d.v))

    interface MatrixDataPoint {
      x: number
      y: number
      v: number
    }

    interface ChartCallbackContext {
      chart: Chart
      dataset: { data: MatrixDataPoint[] }
      dataIndex: number
    }

    const chart = new Chart(ctx, {
      type: 'matrix',
      data: {
      datasets: [
        {
        label: title,
        data: matrixData,
        backgroundColor: (ctx: ChartCallbackContext) => {
          const dataPoint = ctx.dataset.data[ctx.dataIndex]
          if (!dataPoint) return 'rgba(54, 162, 235, 0)'
          const val = dataPoint.v
          const alpha = maxVal > 0 ? val / maxVal : 0
          return `rgba(54, 162, 235, ${alpha})`
        },
        width: ({ chart }: { chart: Chart }) => {
          if (!chart.chartArea) return 0
          return (chart.chartArea.right - chart.chartArea.left) / labels.length - 1
        },
        height: ({ chart }: { chart: Chart }) => {
          if (!chart.chartArea) return 0
          return (chart.chartArea.bottom - chart.chartArea.top) / labels.length - 1
        }
        }
      ]
      },
      options: {
      responsive: false,
      maintainAspectRatio: false,
      scales: {
        x: {
        type: 'category',
        labels,
        offset: true,
        grid: { display: false }
        },
        y: {
        type: 'category',
        labels: [...labels].reverse(),
        offset: true,
        grid: { display: false }
        }
      },
      plugins: {
        tooltip: {
        callbacks: {
          title: () => title,
          label: (ctx: { dataset: { data: MatrixDataPoint[] }; dataIndex: number }) => {
          const d = ctx.dataset.data[ctx.dataIndex]
          const trueLabel = labels[labels.length - 1 - d.y]
          const predLabel = labels[d.x]
          return `${trueLabel} → ${predLabel}: ${d.v}`
          }
        }
        },
        legend: { display: false }
      }
      }
    })

    ;(canvasRef.current as any).chartInstance = chart
  }, [data, labels, title])

  return (
    <canvas
      ref={canvasRef}
      width={width}
      height={height}
      className="w-full h-full"
    />
  )
}

export default D3ConfusionMatrix
