'use client'

import React, { useEffect, useState } from 'react'
import 'chartjs-chart-matrix'
import Head from 'next/head'
import { fetchData } from '../../services/backendService2'
import D3ConfusionMatrix from '../../components/D3ConfusionMatrix'

interface KPI {
  rowKey: string
  value: string
  status?: string
}

interface PlotDataPoint {
  x: number
  y: number
  value?: number
  exceedsThreshold: boolean
}

interface TableDataPoint {
  id: string
  timePeriod: string
  status: string
}

interface OutletsExceedingThreshold {
  id: string
  y_true: number
  y_pred: number
  percentage_error: number
}

interface Coverage {
  total_points?: number
  warning_coverage?: number
  drift_coverage?: number
}

interface Clusters {
  warning?: string[]
  drift?: string[]
  combined?: string[]
}

interface BackwardAnalysis {
  backward_10_percent_drift?: boolean
  backward_10_percent_warning?: boolean
}

interface ErrorDataResponse {
  plotData: PlotDataPoint[]
  tableData: TableDataPoint[]
}

export default function Mode3Page(): JSX.Element {
  const [businessUnit, setBusinessUnit] = useState('CCS')
  const [useCase, setUseCase] = useState('CC-Di')
  const [kpis, setKpis] = useState<KPI[]>([])
  const [errors, setErrors] = useState<ErrorDataResponse>({ plotData: [], tableData: [] })
  const [outletsExceedingThreshold, setOutletsExceedingThreshold] = useState<OutletsExceedingThreshold[]>([])
  const [stateVal, setStateVal] = useState('Unknown')
  const [coverage, setCoverage] = useState<Coverage>({})
  const [clusters, setClusters] = useState<Clusters>({})
  const [backwardAnalysis, setBackwardAnalysis] = useState<BackwardAnalysis>({})
  const [currentPeriod, setCurrentPeriod] = useState('N/A')
  const [totalOutlets, setTotalOutlets] = useState(0)
  const [outletsExceedingThresholdCount, setOutletsExceedingThresholdCount] = useState(0)
  const [xaiExplanation, setXaiExplanation] = useState('No explanation available')
  const [loading, setLoading] = useState(true)

  // for drawing the heatmap
  const [confusionGrid, setConfusionGrid] = useState<number[][]>([])
  const [labels, setLabels] = useState<string[]>([])

  useEffect(() => {
    async function init() {
      setLoading(true)
      try {
        const savedBU = localStorage.getItem('businessUnit')
        const savedUC = localStorage.getItem('useCase')
        if (savedBU) setBusinessUnit(savedBU)
        if (savedUC) setUseCase(savedUC)

        const {
          kpis: fetchedKpis,
          errors: fetchedErrors,
          outletsExceedingThreshold: fetchedOutlets,
          state: fetchedState,
          coverage: fetchedCoverage,
          clusters: fetchedClusters,
          backwardAnalysis: fetchedBackwardAnalysis,
          currentPeriod: fetchedCurrentPeriod,
          totalOutlets: fetchedTotalOutlets,
          outletsExceedingThresholdCount: fetchedOutletsExceedingThresholdCount,
          xaiExplanation: fetchedXai
        } = await fetchData()

        setKpis(fetchedKpis)
        setErrors(fetchedErrors)
        setOutletsExceedingThreshold(fetchedOutlets)
        setStateVal(fetchedState)
        setCoverage(fetchedCoverage)
        setClusters(fetchedClusters)
        setBackwardAnalysis(fetchedBackwardAnalysis)
        setCurrentPeriod(fetchedCurrentPeriod)
        setTotalOutlets(fetchedTotalOutlets)
        setOutletsExceedingThresholdCount(fetchedOutletsExceedingThresholdCount)
        setXaiExplanation(fetchedXai)

        // rebuild confusion matrix grid from plotData
        const xs = fetchedErrors.plotData.map(p => p.x)
        const ys = fetchedErrors.plotData.map(p => p.y)
        const maxX = xs.length ? Math.max(...xs) : 0
        const maxY = ys.length ? Math.max(...ys) : 0
        const grid: number[][] = Array.from({ length: maxY + 1 }, () =>
          Array(maxX + 1).fill(0)
        )
        fetchedErrors.plotData.forEach(pt => {
          grid[pt.y][pt.x] = pt.value ?? 0
        })
        setConfusionGrid(grid)
        setLabels(Array.from({ length: maxX + 1 }, (_, i) => i.toString()))
      } catch (err) {
        console.error('Error fetching data:', err)
      } finally {
        setLoading(false)
      }
    }
    init()
  }, [])

  const handleBusinessUnitChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const v = e.target.value
    setBusinessUnit(v)
    setUseCase('')
    localStorage.setItem('businessUnit', v)
  }
  const handleUseCaseChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const v = e.target.value
    setUseCase(v)
    localStorage.setItem('useCase', v)
  }
  const getUseCaseOptions = () => {
    if (businessUnit === 'CCS') return ['CC-Di', 'CC-MT']
    if (businessUnit === 'JMSL') return ['JM-Ch']
    return []
  }

  return (
    <div className="bg-gray-900 min-h-screen flex flex-col">
      <Head>
        <title>Mode 3 | CL Dashboard</title>
        <link
          rel="stylesheet"
          href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0-beta3/css/all.min.css"
        />
      </Head>
      <main className="flex-grow container mx-auto px-4 py-8">

        {/* Header Metrics */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">
            OCTAVE - CL Dashboard
          </h2>
          <p className="text-blue-200 mb-2">
            Current Period: {loading ? 'Loading...' : currentPeriod}
          </p>
          <p className="text-blue-200 mb-2">
            State: {loading ? 'Loading...' : stateVal}
          </p>
          <p className="text-blue-200 mb-2">
            Total Outlets: {loading ? 'Loading...' : totalOutlets}
          </p>
          <p className="text-blue-200 mb-2">
            Outlets Exceeding Threshold:{' '}
            {loading ? 'Loading...' : outletsExceedingThresholdCount}
          </p>
          <p className="text-blue-200 mb-2">
            Coverage – Total Points:{' '}
            {loading ? 'Loading...' : coverage.total_points ?? 'N/A'}
          </p>
          <p className="text-blue-200 mb-2">
            Coverage – Warning:{' '}
            {loading ? 'Loading...' : coverage.warning_coverage ?? 'N/A'}
          </p>
          <p className="text-blue-200 mb-2">
            Coverage – Drift:{' '}
            {loading ? 'Loading...' : coverage.drift_coverage ?? 'N/A'}
          </p>
          <p className="text-blue-200 mb-2">
            Backward Analysis – 10% Drift:{' '}
            {loading
              ? 'Loading...'
              : backwardAnalysis.backward_10_percent_drift
              ? 'Yes'
              : 'No'}
          </p>
          <p className="text-blue-200 mb-2">
            Backward Analysis – 10% Warning:{' '}
            {loading
              ? 'Loading...'
              : backwardAnalysis.backward_10_percent_warning
              ? 'Yes'
              : 'No'}
          </p>

          {/* Business Unit / Use Case */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mt-4">
            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">
                Business Unit
              </h3>
              <select
                className="w-full bg-gray-700 border-blue-600 rounded p-2 text-white"
                value={businessUnit}
                onChange={handleBusinessUnitChange}
              >
                <option value="">Select Business Unit</option>
                <option value="CCS">CCS</option>
                <option value="JMSL">JMSL</option>
              </select>
            </div>
            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">
                Use Case
              </h3>
              <select
                className="w-full bg-gray-700 border-blue-600 rounded p-2 text-white"
                value={useCase}
                onChange={handleUseCaseChange}
                disabled={!businessUnit}
              >
                <option value="">
                  {businessUnit
                    ? 'Select Use Case'
                    : 'Select Business Unit first'}
                </option>
                {getUseCaseOptions().map(o => (
                  <option key={o} value={o}>
                    {o}
                  </option>
                ))}
              </select>
            </div>
            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">
                Short Code
              </h3>
              <input
                type="text"
                value={useCase || '-'}
                readOnly
                className="w-full bg-gray-700 border-blue-600 rounded p-2 text-white"
              />
            </div>
            <div className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50">
              <h3 className="text-lg font-medium text-blue-200 mb-2">
                Runtime
              </h3>
              <input
                type="text"
                value="2h 45m"
                readOnly
                className="w-full bg-gray-700 border-blue-600 rounded p-2 text-white"
              />
            </div>
          </div>
        </div>

        {/* KPI Cards */}
        <div className="grid grid-cols-1 md:grid-cols-5 gap-4 mb-6">
          {kpis.map(kpi => (
            <div
              key={kpi.rowKey}
              className="bg-blue-900/30 p-4 rounded-lg border border-blue-800/50"
            >
              <h3 className="text-lg font-medium text-blue-200 mb-2">
                {kpi.rowKey}
              </h3>
              <p
                className={`text-xl ${
                  kpi.status === 'Alert'
                    ? 'text-yellow-400'
                    : kpi.status === 'Warning'
                    ? 'text-yellow-400'
                    : kpi.status === 'Error'
                    ? 'text-red-400'
                    : 'text-green-400'
                }`}
              >
                {loading ? 'Loading...' : kpi.value}
              </p>
            </div>
          ))}
        </div>

        {/* Confusion Matrix Heatmap */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">
            Confusion Matrix
          </h2>
          <div className="h-64 bg-gray-700 rounded p-4">
            {loading ? (
              <div className="flex items-center justify-center h-full">
                <div className="text-white">Loading matrix...</div>
              </div>
            ) : (
              <D3ConfusionMatrix
                data={confusionGrid}
                labels={labels}
                title="Confusion Matrix"
                width={500}
                height={500}
              />
            )}
          </div>
        </div>

        {/* Misclassified Table & Outlets Exceeding */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
          {/* Misclassified */}
          <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 border border-gray-700">
            <h2 className="text-2xl font-semibold text-blue-300 mb-4">
              Misclassified Table
            </h2>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-700">
                <thead>
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">
                      ID
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">
                      True → Pred
                    </th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-700">
                  {loading ? (
                    <tr>
                      <td
                        colSpan={2}
                        className="px-6 py-4 text-center text-sm text-white"
                      >
                        Loading…
                      </td>
                    </tr>
                  ) : errors.tableData.length > 0 ? (
                    errors.tableData.map((r, i) => (
                      <tr key={i} className="bg-red-900/10">
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-white">
                          {r.id}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-red-300">
                          {r.timePeriod}
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td
                        colSpan={2}
                        className="px-6 py-4 text-center text-sm text-white"
                      >
                        No misclassified data
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>

          {/* Outlets Exceeding */}
          <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 border border-gray-700">
            <h2 className="text-2xl font-semibold text-red-300 mb-4">
              Outlets Exceeding Threshold
            </h2>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-red-800/50">
                <thead>
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-200 uppercase tracking-wider">
                      ID
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-200 uppercase tracking-wider">
                      True
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-200 uppercase tracking-wider">
                      Pred
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-red-200 uppercase tracking-wider">
                      % Error
                    </th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-red-800/50">
                  {loading ? (
                    <tr>
                      <td
                        colSpan={4}
                        className="px-6 py-4 text-center text-sm text-white"
                      >
                        Loading…
                      </td>
                    </tr>
                  ) : outletsExceedingThreshold.length > 0 ? (
                    outletsExceedingThreshold.map(o => (
                      <tr key={o.id}>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-white">
                          {o.id}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-white">
                          {o.y_true.toFixed(2)}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-white">
                          {o.y_pred.toFixed(2)}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-red-400">
                          {o.percentage_error.toFixed(2)}%
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td
                        colSpan={4}
                        className="px-6 py-4 text-center text-sm text-white"
                      >
                        None
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        {/* XAI Explanation */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">
            XAI Result
          </h2>
          <div className="space-y-3 text-white">
            {loading ? (
              <p>Loading explanation...</p>
            ) : xaiExplanation ? (
              <div
                className="prose prose-invert"
                dangerouslySetInnerHTML={{ __html: xaiExplanation }}
              />
            ) : (
              <p className="text-red-400">No explanation available</p>
            )}
          </div>
        </div>
      </main>
    </div>
  )
}
